@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);


/* 
LOGO FONT

LOGO SUB-HEADING FONT
@import url('https://fonts.googleapis.com/css2?family=Lacquer&display=swap'); 
*/

@font-face {
  font-family: Zombie;
  src: url(/etherorcs-poc/static/media/ZOMBIE.227a6f01.TTF);
  }
  
/* HEADING AND INPUT FONTS */
@font-face {
	font-family: VCR OSD Mono;
	src: url(/etherorcs-poc/static/media/VCR_OSD_MONO_1.001.10cf96d5.ttf);
}


/* TEXT FONT */
@font-face {
	font-family: RPGSystem;
	src: url(/etherorcs-poc/static/media/RPGSystem.3e91fe09.ttf);
}

body{
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -ms-interpolation-mode: nearest-neighbor;
  image-rendering: -moz-crisp-edges;
  image-rendering: pixelated;
  text-shadow: 2px 2px 1px rgb(0, 0, 0);
  font-size: 1.6rem;
}

a {
  text-transform: uppercase;
  color: #0f0;
  text-decoration: none;
  font-size: 1.2rem;
}

img{
  max-width: 100%;
}


ul li {
  list-style: square;
}

.heading{
  font-family: 'VCR OSD Mono', cursive;
  color: #ff5;
  text-shadow: 3px 3px #a50;
}

.green-console-button{
  border-radius: 0;
  color: #0f0;
  background-color: #040;
  text-decoration: none;
  display: inline-block;
  box-shadow: 10px 10px 0 rgb(0 0 0 / 75%);
  position: relative;
  font-family: VCR OSD Mono,monospace;
  text-shadow: 2px 2px 0 rgb(0 0 0 / 75%);
  font-size: 1.2rem;
  letter-spacing: 0;
}

.green-console-button:hover{
  background-color: #206e2d;
}

.yellow-console-button{
  border-radius: 0;
  color: #ff5;
  background-color: #a50;
  text-decoration: none;
  display: inline-block;
  box-shadow: 10px 10px 0 rgb(0 0 0 / 75%);
  position: relative;
  font-family: VCR OSD Mono,monospace;
  text-shadow: 2px 2px 0 rgb(0 0 0 / 75%);
  font-size: 1.2rem;
  letter-spacing: 0;
}

.yellow-console-button:hover{
  background-color: rgb(172, 110, 48);
}


.mobile-only{
  display:none;
}


@media only screen and (max-width: 768px) {
  .desktop-only{
    display:none;
  }
  .mobile-only{
    display:block;
  }

  .heading{
    font-size: 2.8rem;
  }
}

.orc{
  border-radius: 0;
  background: #00000040;
  cursor: pointer;
}

.orc.selected{
  border-radius: 0;
  color: #0f0;
  background-color: #040;
  box-shadow: 10px 10px 0 rgb(0 0 0 / 75%);
}

.console{
  font-family: 'VT323', monospace;
}

.console-link{
  font-size: 1.8rem;
  cursor: pointer;
  display: block;
}

.console-logo{
  text-shadow: none;
    background: linear-gradient(90deg,#ff00db,#0aa, yellow,#41ff00,#0aa,#41ff00);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}



.home {
  background: url(/etherorcs-poc/static/media/website-bg-1.c0eb8eaa.png);
  
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  height: 0;
  padding-top: 46.190225959%; /* (img-height / img-width * container-width) */
  /* (879 / 1903 * 100) */
  position: relative;
}



.training-yard-img {
  background: url(/etherorcs-poc/static/media/medieval-training-yard.f819c715.png);
  background-repeat: no-repeat;
  background-size: 100%;
}

.shop-img {
  background: url(/etherorcs-poc/static/media/Shop.a06bc1fa.png);
  background-repeat: no-repeat;
  background-size: contain;
}

.home-content {
  position: absolute;
  top: 0;
  width: 100%;
  background: repeating-linear-gradient(rgba(0, 255, 76, 0.1), rgba(255,0,0,0.1) 2px, transparent 2px, transparent 4px);
  bottom: 0;
}

.logo {
  width:100%;
  max-width: 500px;
  margin-top: 3em;
}

.slogan {
  margin-top: -15px;
  text-shadow: 0px 3px 0px rgb(0, 0, 0);
}

.banner-text {
  color:#fff;
  text-shadow: 3px 3px 1px black;
  padding: 10px;
}

.banner-mobile-extended {
  padding-top: 0;
  background: #000;
}

@media only screen and (max-width: 1366px) {
  .logo {
    margin-top: 0;
  }

  .slogan {
    font-size: 2rem;
  }
}


.footer{
  position: relative;
}

.footer-image-left{
  position: absolute;
  left:0;
  bottom:0;
}

.footer-image-right{
  position: absolute;
  right:0;
  bottom:0;
}

.button-style{
  border-radius: 0;
  color: white;
  background: #832626;
}
.button-style:hover{
  background: #a73f3f;
}

.button-style-alt{
 
  color: white;
  text-shadow: 1px 1px 1px #000;
  background: #2b4777;
}
.button-style-alt:hover{
  background: #315085;
}

.team-member-name {
  font-family: 'VCR OSD Mono';
  text-transform: uppercase;
  font-size: 2rem;
  text-shadow: -1px 2px 0px #000;
  letter-spacing: 4px;
}

@media only screen and (max-width: 768px) {
  .slogan {
    font-size: 1.5rem;
  }

  .home {
    background-image: url(/etherorcs-poc/static/media/website-bg-1-mobile.3e3e198f.png);
    padding-top: 95.8515283843%; /* (img-height / img-width * container-width) */
    /* (878 / 916 * 100) */
  }
  .logo {
    margin-top: 0;
  }

  .banner-buttons{
    padding-top: 0;
    margin-top: 0;
  }
}



.header{
    background:none;
}

.social-button svg{
    fill:white;
    padding-right: 10px;
    
}
.social-button{
    margin-right:15px;
    font-family: VCR OSD Mono,monospace;
}

.nav-anchor{
    color: white;
    text-decoration: none;
}

.menu-toggle{
    position:fixed;
    top: 6px;
    left: 20px;
}

@media only screen and (max-width: 768px) {
    .social-button{
        margin-right:0;
    }

    .menu-toggle{
        background-color:#000;
        box-shadow: 0px 0px 3px #fff;
    }
}

.typed-cursor{
    opacity: 1;
    animation: typedjsBlink 0.7s infinite;
    -webkit-animation: typedjsBlink 0.7s infinite;
    animation: typedjsBlink 0.7s infinite;
  }
  @keyframes typedjsBlink{
    50% { opacity: 0.0; }
  }
  @-webkit-keyframes typedjsBlink{
    0% { opacity: 1; }
    50% { opacity: 0.0; }
    100% { opacity: 1; }
  }
  .typed-fade-out{
    opacity: 0;
    transition: opacity .25s;
    -webkit-animation: 0;
    animation: 0;
  }
