
.home {
  background: url(../assets/images/website-bg-1.png);
  
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  height: 0;
  padding-top: 46.190225959%; /* (img-height / img-width * container-width) */
  /* (879 / 1903 * 100) */
  position: relative;
}



.training-yard-img {
  background: url(../assets/images/medieval-training-yard.png);
  background-repeat: no-repeat;
  background-size: 100%;
}

.shop-img {
  background: url(../assets/images/Shop.png);
  background-repeat: no-repeat;
  background-size: contain;
}

.home-content {
  position: absolute;
  top: 0;
  width: 100%;
  background: repeating-linear-gradient(rgba(0, 255, 76, 0.1), rgba(255,0,0,0.1) 2px, transparent 2px, transparent 4px);
  bottom: 0;
}

.logo {
  width:100%;
  max-width: 500px;
  margin-top: 3em;
}

.slogan {
  margin-top: -15px;
  text-shadow: 0px 3px 0px rgb(0, 0, 0);
}

.banner-text {
  color:#fff;
  text-shadow: 3px 3px 1px black;
  padding: 10px;
}

.banner-mobile-extended {
  padding-top: 0;
  background: #000;
}

@media only screen and (max-width: 1366px) {
  .logo {
    margin-top: 0;
  }

  .slogan {
    font-size: 2rem;
  }
}


.footer{
  position: relative;
}

.footer-image-left{
  position: absolute;
  left:0;
  bottom:0;
}

.footer-image-right{
  position: absolute;
  right:0;
  bottom:0;
}

.button-style{
  border-radius: 0;
  color: white;
  background: #832626;
}
.button-style:hover{
  background: #a73f3f;
}

.button-style-alt{
 
  color: white;
  text-shadow: 1px 1px 1px #000;
  background: #2b4777;
}
.button-style-alt:hover{
  background: #315085;
}

.team-member-name {
  font-family: 'VCR OSD Mono';
  text-transform: uppercase;
  font-size: 2rem;
  text-shadow: -1px 2px 0px #000;
  letter-spacing: 4px;
}

@media only screen and (max-width: 768px) {
  .slogan {
    font-size: 1.5rem;
  }

  .home {
    background-image: url(../assets/images/website-bg-1-mobile.png);
    padding-top: 95.8515283843%; /* (img-height / img-width * container-width) */
    /* (878 / 916 * 100) */
  }
  .logo {
    margin-top: 0;
  }

  .banner-buttons{
    padding-top: 0;
    margin-top: 0;
  }
}


