/* 
LOGO FONT

LOGO SUB-HEADING FONT
@import url('https://fonts.googleapis.com/css2?family=Lacquer&display=swap'); 
*/
@import url('https://fonts.googleapis.com/css2?family=VT323&display=swap');

@font-face {
  font-family: Zombie;
  src: url(assets/fonts/ZOMBIE.TTF);
  }
  
/* HEADING AND INPUT FONTS */
@font-face {
	font-family: VCR OSD Mono;
	src: url(assets/fonts/VCR_OSD_MONO_1.001.ttf);
}


/* TEXT FONT */
@font-face {
	font-family: RPGSystem;
	src: url(assets/fonts/RPGSystem.ttf);
}

body{
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -ms-interpolation-mode: nearest-neighbor;
  image-rendering: -webkit-optimize-contrast;
  image-rendering: -moz-crisp-edges;
  image-rendering: -o-pixelated;
  image-rendering: pixelated;
  text-shadow: 2px 2px 1px rgb(0, 0, 0);
  font-size: 1.6rem;
}

a {
  text-transform: uppercase;
  color: #0f0;
  text-decoration: none;
  font-size: 1.2rem;
}

img{
  max-width: 100%;
}


ul li {
  list-style: square;
}

.heading{
  font-family: 'VCR OSD Mono', cursive;
  color: #ff5;
  text-shadow: 3px 3px #a50;
}

.green-console-button{
  border-radius: 0;
  color: #0f0;
  background-color: #040;
  text-decoration: none;
  display: inline-block;
  -webkit-box-shadow: 10px 10px 0 rgb(0 0 0 / 75%);
  box-shadow: 10px 10px 0 rgb(0 0 0 / 75%);
  position: relative;
  font-family: VCR OSD Mono,monospace;
  text-shadow: 2px 2px 0 rgb(0 0 0 / 75%);
  font-size: 1.2rem;
  letter-spacing: 0;
}

.green-console-button:hover{
  background-color: #206e2d;
}

.yellow-console-button{
  border-radius: 0;
  color: #ff5;
  background-color: #a50;
  text-decoration: none;
  display: inline-block;
  -webkit-box-shadow: 10px 10px 0 rgb(0 0 0 / 75%);
  box-shadow: 10px 10px 0 rgb(0 0 0 / 75%);
  position: relative;
  font-family: VCR OSD Mono,monospace;
  text-shadow: 2px 2px 0 rgb(0 0 0 / 75%);
  font-size: 1.2rem;
  letter-spacing: 0;
}

.yellow-console-button:hover{
  background-color: rgb(172, 110, 48);
}


.mobile-only{
  display:none;
}


@media only screen and (max-width: 768px) {
  .desktop-only{
    display:none;
  }
  .mobile-only{
    display:block;
  }

  .heading{
    font-size: 2.8rem;
  }
}

.orc{
  border-radius: 0;
  background: #00000040;
  cursor: pointer;
}

.orc.selected{
  border-radius: 0;
  color: #0f0;
  background-color: #040;
  -webkit-box-shadow: 10px 10px 0 rgb(0 0 0 / 75%);
  box-shadow: 10px 10px 0 rgb(0 0 0 / 75%);
}

.console{
  font-family: 'VT323', monospace;
}

.console-link{
  font-size: 1.8rem;
  cursor: pointer;
  display: block;
}

.console-logo{
  text-shadow: none;
    background: -webkit-gradient(linear,left top,right top,from(#ff00db),color-stop(#551a8b),color-stop(#0aa),to(#41ff00));
    background: linear-gradient(90deg,#ff00db,#0aa, yellow,#41ff00,#0aa,#41ff00);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

